import React from "react";
import { Dropdown, Badge, Button, Icon } from "rsuite";

import { ReactComponent as SettingsIcon } from "../images/icons/settings-icon.svg";
import { ReactComponent as NotificationIcon } from "../images/icons/notification-icon.svg";
import "../styles/TopNav.scss";
import withStyles from "react-jss";

const styles = {
  sideBarToggle: {
    display: "inline-block",
    marginRight: 17,
  },
  "@media (min-width: 480px)": {
    sideBarToggle: {
      display: "none",
    },
  },
};

class TopNav extends React.Component {
  render() {
    const { classes, onToggleOpen } = this.props;
    return (
      <div className="top-nav">
        <div className="container-fluid">
          <div className="left">
            <Button
              className={classes.sideBarToggle}
              onClick={() => onToggleOpen()}
            >
              <Icon icon="bars" />
            </Button>
            <Dropdown title="Create New" appearance="primary">
              <Dropdown.Item>Option 1</Dropdown.Item>
              <Dropdown.Item>Option 2</Dropdown.Item>
              <Dropdown.Item>Option 3</Dropdown.Item>
              <Dropdown.Item>Option 4</Dropdown.Item>
            </Dropdown>
          </div>
          <div className="right">
            <SettingsIcon className="settings-icon" width="20" height="20" />

            <Badge className="notification-icon">
              <NotificationIcon width="20" height="20" />
            </Badge>

            <div className="user">
              <div className="icon"></div>
              <div className="name">Tom Jones</div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default withStyles(styles)(TopNav);
