import React from "react";
import classNames from "classnames";
import { Container } from "rsuite";

import TopNav from "../TopNav";
import RV_Sidebar from "../RV_Sidebar";

class MainLayout extends React.Component {
  state = {
    sideDrawerOpen: false,
  };

  drawerToggleClickerHandler = () => {
    this.setState((prevState) => {
      return { sideDrawerOpen: !prevState.sideDrawerOpen };
    });
  };

  render() {
    const { children } = this.props;
    const { sideDrawerOpen } = this.state;

    return (
      <div className="dashboard-page">
        <main>
          <Container>
            <div class="rs-sidebar-wrapper fixed">
              <RV_Sidebar isOpen={this.state.sideDrawerOpen} />
            </div>
            <div>
              <Container
                className={classNames({
                  "page-context": true,
                  stretched: this.props.isSideMenuOpen === false,
                })}
              >
                <TopNav onToggleOpen={this.drawerToggleClickerHandler} />
                {children}
              </Container>
            </div>
          </Container>
        </main>
      </div>
    );
  }
}

export default MainLayout;
